.Dashboard {
  padding: 48px 0px; }
  .Dashboard .title {
    font-family: Usual;
    font-weight: 700;
    font-size: 2rem;
    align-items: flex-start;
    color: #272E4C; }
  .Dashboard .sub-title {
    font-family: Usual;
    font-weight: 600;
    font-size: 1.25rem;
    color: #9EA3AC;
    margin-bottom: 16px; }
  .Dashboard .content {
    min-height: 200px;
    border-radius: 12px;
    margin-bottom: 16px;
    overflow: hidden;
    box-shadow: 0px 3px 6px #272E4C19; }
  .Dashboard .loading-indicator {
    color: green [500];
    justify-content: 'center';
    margin: auto;
    z-index: -1; }
  .Dashboard .card-header {
    height: 104px;
    background-color: #FAFCFE;
    padding: 16px; }
  .Dashboard .card-graph-header {
    padding: 16px 0px;
    margin: 0px 16px; }
  .Dashboard .card-content {
    min-height: 200px;
    padding: 0px 16px; }
  .Dashboard .card-footer {
    padding: 16px; }
  .Dashboard .dashboard-temp-graph-img-report {
    height: 240px;
    max-width: 100%;
    margin: 16px 0px; }
  .Dashboard .dashboard-temp-graph-img {
    width: 100%;
    margin: 16px 0px; }
  .Dashboard .dashboard-graph-title {
    font-family: Usual;
    font-weight: 600;
    font-size: 1.5rem;
    color: #151C1B; }
  .Dashboard .dashboard-count {
    font-size: 2.25rem; }
  .Dashboard .dashboard-count-description {
    font-family: Usual;
    font-weight: 600;
    font-size: 0.875rem;
    color: #151C1B; }
  .Dashboard .dashboard-table-hg-even-row {
    background-color: #FFF1F1; }
  .Dashboard .dashboard-table-even-row {
    background-color: #FFFFFF; }
  .Dashboard .dashboard-table-hg-odd-row {
    background-color: #F7EAEC; }
  .Dashboard .dashboard-table-odd-row {
    background-color: #FAFCFE; }
  .Dashboard .dashboard-table-header-cell {
    font-family: Usual;
    font-weight: 700;
    font-size: 0.875rem;
    color: #3C394A; }
  .Dashboard .dashboard-table-data-cell {
    font-family: Usual;
    font-size: 0.875rem;
    color: #151C1B; }
  .Dashboard .dashboard-list-item-text {
    font-family: Usual;
    font-size: 0.875rem;
    color: #151C1B; }
  .Dashboard .dashboard-list-item-link {
    font-family: Usual;
    font-weight: 700;
    font-size: 0.875rem;
    color: #3AB77D;
    text-decoration: none; }
  .Dashboard .dashboard-view-more-link {
    font-family: Usual;
    font-weight: 700;
    font-size: 0.875rem;
    color: #3AB77D;
    text-decoration: none; }

